import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import AddIcon from '@/components/icons/AddIcon';
import { Link } from 'react-router-dom';
import ComplaintsStatisticsCards from '../components/statistics/complaintsStatisticsCards';
import ComplaintsComponent from '../components/complaints/ComplaintsComponent';

export default function ComplaintsPag() {
    return (
        <div className="mt-6 w-full">
            <ComplaintsStatisticsCards />
            <div className="flex flex-row items-center justify-between mt-6">
                <h1 className="font-bold text-[#080614] text-[24px] tracking-normal">قائمة الشكاوي</h1>
                <Link to="/customer-relations/complaints/add">
                    <PrimaryButton className="!px-[25px] !py-3">
                        <AddIcon />
                        تسجيل شكوى جديدة
                    </PrimaryButton>
                </Link>
            </div>
            <ComplaintsComponent />
        </div>
    );
}
