import { useEffect, useState } from 'react';
import ComplaintChat from './ComplaintChat';
import ComplaintsList from './complaintsList';
import { Complaint, ComplaintsPagination } from '@/types/complaint.types';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useDebounce } from '@/core/global-hooks';

export default function ComplaintsComponent() {
    const [apparentComplaint, setِِِِِِِِِApparentComplaint] = useState<Complaint | null>(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [complaints, setComplaints] = useState<Complaint[] | null>(null);
    const [pagination, setPagination] = useState<ComplaintsPagination | null>(null);
    const [search, setSearch] = useState('');
    const [filterValue, setFilterValue] = useState('');
    const [refresh, setRefresh] = useState(false);

    const debouncedSearch = useDebounce(search, 150);

    useEffect(() => {
        const fetchComplaints = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.complaint.list,
                    queryParams: { page: currentPage, search: debouncedSearch, status: filterValue },
                });

                // Replace data on first page, append otherwise
                setComplaints((prev) => {
                    if (currentPage === 1) {
                        return response.data.data.data;
                    }
                    return [...(prev || []), ...response.data.data.data];
                });

                setPagination(response.data.data.pagination);
            } catch (error) {
                setError('Failed to load complaints details');
            } finally {
                setLoading(false);
            }
        };
        fetchComplaints();
    }, [currentPage, debouncedSearch, filterValue]);

    useEffect(() => {
        if (apparentComplaint) {
            const fetchComplaint = async () => {
                try {
                    const response = await apiGet({
                        endpoint: endpoints.complaint.show + apparentComplaint.id,
                        queryParams: { page: currentPage, search, status: filterValue },
                    });
                    setِِِِِِِِِApparentComplaint(response.data.data);
                    setError(null);
                } catch (error) {
                    setError('Failed to load complaint details');
                }
            };
            fetchComplaint();
        }
    }, [refresh]);

    return (
        <div className="mt-6 w-full flex flex-row gap-6 mb-10">
            <ComplaintsList
                setِِِِِِِِِApparentComplaint={setِِِِِِِِِApparentComplaint}
                apparentComplaint={apparentComplaint}
                complaints={complaints}
                currentPage={currentPage}
                error={error}
                loading={loading}
                pagination={pagination}
                filterValue={filterValue}
                setCurrentPage={setCurrentPage}
                setPagination={setPagination}
                setSearch={setSearch}
                setFilterValue={setFilterValue}
            />
            <ComplaintChat
                apparentComplaint={apparentComplaint}
                setِِِِِِِِِApparentComplaint={setِِِِِِِِِApparentComplaint}
                setRefresh={setRefresh}
                refresh={refresh}
            />
        </div>
    );
}
