import { CustomSelect } from '@/components/dropDown/Dropdown';
import { Input } from '@/components/ui/input';
import SearchIcon from '@/components/icons/Search';
import ChatItem from './ChatItem';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { Complaint, ComplaintsPagination } from '@/types/complaint.types';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';

interface Props {
    setِِِِِِِِِApparentComplaint: (complaint: Complaint) => void;
    apparentComplaint: Complaint | null;
    complaints: Complaint[] | null;
    pagination: ComplaintsPagination | null;
    setPagination: (pagination: ComplaintsPagination | null) => void;
    loading: boolean;
    error: string | null;
    currentPage: number;
    setCurrentPage: (currentPage: number) => void;
    setSearch: (search: string) => void;
    setFilterValue: (filterValue: string) => void;
    filterValue: string;
}

export default function ComplaintsList({
    setِِِِِِِِِApparentComplaint,
    apparentComplaint,
    complaints,
    currentPage,
    error,
    loading,
    pagination,
    filterValue,
    setCurrentPage,
    setPagination,
    setSearch,
    setFilterValue,
}: Props) {
    const loadMoreComplaints = () => {
        setCurrentPage(currentPage + 1);
    };

    const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearch(e.target.value);
        setPagination(null);
    };

    const handleFilter = (value: string) => {
        setFilterValue(value);
        setPagination(null);
    };

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!complaints) {
        return (
            <div className="mt-6 w-1/3 mb-[102px] text-center py-20">
                <h2 className="text-xl">Complaints not found</h2>
            </div>
        );
    }
    return (
        <div className="w-1/3">
            <div className="rounded-[13px] overflow-hidden">
                <div className="bg-primery-blue-50 px-4 py-[13px]">
                    <div className="flex flex-row items-center gap-2">
                        <div className="flex flex-row items-center w-full h-10 border border-white-blue-300 bg-white-blue-50 p-2 rounded-[6px]">
                            <Input
                                placeholder="ابحث باسم  العميل"
                                className="text-white-blue-800 p-0 tracking-normal text-base font-normal border-none outline-none focus-visible:ring-0 placeholder:text-[#B6B5BF]"
                                onChange={handleSearch}
                            />
                            <button className="bg-transparent border-none">
                                <SearchIcon className="text-[#B6B5BF]" />
                            </button>
                        </div>
                        <CustomSelect
                            options={[
                                { label: 'معلقه', value: 'pending' },
                                { label: 'قيد التقدم', value: 'in_progress' },
                                { label: 'تم حلها', value: 'resolved' },
                                { label: 'الكل', value: '' },
                            ]}
                            value={filterValue}
                            onValueChange={handleFilter}
                            placeholder="الحاله"
                            width="min-w-[77px]"
                            className="h-10"
                        />
                    </div>
                </div>
                <div className="border border-[#E8E7EA] max-h-[740px] overflow-y-auto custom-scrollbar">
                    {complaints.map((complaint) => (
                        <ChatItem
                            key={complaint.id}
                            complaint={complaint}
                            setِِِِِِِِِApparentComplaint={setِِِِِِِِِApparentComplaint}
                            apparentComplaint={apparentComplaint}
                        />
                    ))}
                    {complaints.length === 0 && (
                        <div className="text-center py-20">
                            <h2 className="text-xl">لا توجد نتائج لعرضها</h2>
                        </div>
                    )}
                    {pagination && complaints.length < pagination.total && (
                        <div className="flex justify-center mt-4">
                            <PrimaryButton onClick={loadMoreComplaints} className="text-base font-normal">
                                تحميل المزيد
                            </PrimaryButton>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
