import { cn } from '@/lib/utils';
import { Complaint } from '@/types/complaint.types';
import { format } from 'date-fns';

interface Props {
    complaint: Complaint;
    setِِِِِِِِِApparentComplaint: (complaint: Complaint) => void;
    apparentComplaint: Complaint | null;
}

export default function ChatItem({ complaint, setِِِِِِِِِApparentComplaint, apparentComplaint }: Props) {
    const selectComplaint = () => {
        setِِِِِِِِِApparentComplaint(complaint);
    };

    return (
        <div
            onClick={selectComplaint}
            className={cn(
                'flex flex-col justify-between cursor-pointer gap-2 bg-white-blue-50 p-4 border-b border-[#D2CDD1] transition-all duration-200',
                {
                    'bg-primery-blue-50': apparentComplaint?.id === complaint.id,
                },
            )}
        >
            <div className="flex flex-row justify-between items-center">
                <p className="text-sm font-normal text-[#120E2B] tracking-normal">{complaint.customer.full_name}</p>
                <div className="w-[14px] h-[14px] bg-[#FEE2E2] rounded-full"></div>
            </div>
            <div className="flex flex-row justify-between items-center">
                <p className="text-xs font-normal text-[#70686D] tracking-normal">{complaint.title}</p>
                <span className="text-[9px] font-normal text-[#70686D] tracking-normal">
                    {format(new Date(complaint.created_at), 'dd/MM/yyyy')}
                </span>
            </div>
        </div>
    );
}
