import { Card } from '@/types/statistics';
import StatisticsCards from '@/components/cards/StatisticsCards';
import DirectInboxIcon from '@/components/icons/statistics/DirectInboxIcon';
import MessageTimeIcon from '@/components/icons/statistics/MessageTime';
import { useEffect, useState } from 'react';
import { ComplaintDashboard } from '../../types/complaintDashboard.types';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';

export default function ComplaintsStatisticsCards() {
    const [stats, setStats] = useState<ComplaintDashboard | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.complaint.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'الرسائل الواردة', icon: <DirectInboxIcon />, num: stats?.in_progress_complaints, percent: 0 },
        { text: 'رسائل قيد الانتظار', icon: <MessageTimeIcon />, num: stats?.pending_complaints, percent: 0 },
        { text: 'رسائل تم الاجابة عليها', icon: <MessageTimeIcon />, num: stats?.resolved_complaints, percent: 0 },
    ];
    return <StatisticsCards cards={cards} />;
}
