import user from '@/assets/imgs/user.png';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { Complaint } from '@/types/complaint.types';
import { format } from 'date-fns';
import Reply from './Reply';
import { useState } from 'react';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';

interface Props {
    apparentComplaint: Complaint | null;
    setِِِِِِِِِApparentComplaint: (complaint: Complaint | null) => void;
    refresh: boolean;
    setRefresh: (currentPage: boolean) => void;
}
export default function ComplaintChat({
    apparentComplaint,
    setِِِِِِِِِApparentComplaint,
    refresh,
    setRefresh,
}: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);

    const handleCloseComplaint = async () => {
        setLoading(true);
        setError(null); // Clear previous errors
        try {
            await apiPost({
                endpoint: endpoints.complaint.update + apparentComplaint?.id,
                body: { status: 'resolved', customer_id: apparentComplaint?.customer.id },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });
            setError(null);
            setRefresh(!refresh);
        } catch (error: any) {
            // Handle generic error
            let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
            if (error.response) {
                errorMessage = error.response.data.msg || errorMessage;
            } else if (error.request) {
                errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
            }
            setError(errorMessage);
        } finally {
            setLoading(false);
        }
    };

    if (!apparentComplaint) return null;
    return (
        <div className="w-2/3 rounded-xl overflow-hidden">
            <div className="bg-primery-blue-50 px-4 py-2 flex flex-row items-center justify-between mb-3">
                <div className="flex flex-row items-center justify-between gap-7">
                    <img className="w-10 h-10 rounded-full" src={user} alt="" />
                    <div className="flex flex-col items-start justify-center gap-2">
                        <p className="text-sm font-normal text-[#120E2B] tracking-normal">
                            {apparentComplaint.customer.full_name}
                        </p>
                        <p className="text-sm font-normal text-[#70686D] tracking-normal">نوع وتاريخ العضوية</p>
                    </div>
                </div>
                <div className="text-sm font-normal text-[#70686D] tracking-normal">
                    {format(new Date(apparentComplaint.created_at), 'dd/MM/yyyy')}
                </div>
            </div>
            <div className="flex flex-col gap-4 px-4 pt-2">
                <p className="text-base font-semibold text-[#100147] tracking-normal">تفاصيل الشكوى</p>
                <div className="flex flex-row gap-4">
                    <p className="text-base w-[110px] font-semibold text-[#100147] tracking-normal">رقم الشكوى</p>
                    <p className="bg-[#F5F4F5] w-[195px] border border-[#E1DEE0] text-[#9E939A] text-xs font-semibold tracking-normal px-4 py-2">
                        {apparentComplaint.id}
                    </p>
                </div>
                <div className="flex flex-row gap-4">
                    <p className="text-base w-[110px] font-semibold text-[#100147] tracking-normal">تاريخ الشكوى</p>
                    <p className="bg-[#F5F4F5] w-[195px] border border-[#E1DEE0] text-[#9E939A] text-xs font-semibold tracking-normal px-4 py-2">
                        {format(new Date(apparentComplaint.created_at), 'dd/MM/yyyy')}
                    </p>
                </div>
                <div className="flex flex-row gap-4">
                    <p className="text-base w-[110px] font-semibold text-[#100147] tracking-normal">حالة الشكوى</p>
                    <p className="bg-[#D1FAE5] w-[195px] border border-[#E1DEE0] text-[#065F46] text-xs text-center font-semibold tracking-normal px-4 py-2">
                        {apparentComplaint.status}
                    </p>
                </div>
            </div>
            <div className="mt-7 px-4">
                <p className="text-base font-semibold text-[#100147] tracking-normal mb-4">نص الشكوى</p>
                <p
                    dangerouslySetInnerHTML={{ __html: apparentComplaint.description }}
                    className="border border-[#E8E7EA] rounded-lg p-2 max-h-[480px] custom-scrollbar overflow-y-auto text-[#575155] text-base font-medium tracking-normal"
                ></p>
            </div>
            <div>
                {
                    <div className="mt-7 px-4">
                        <p className="text-base font-semibold text-[#100147] tracking-normal mb-4">الرد</p>
                        <p className="border border-[#E8E7EA] rounded-lg p-2 max-h-[480px] custom-scrollbar overflow-y-auto text-[#575155] text-base font-medium tracking-normal">
                            {apparentComplaint?.answer}
                        </p>
                    </div>
                }
            </div>
            <Reply apparentComplaint={apparentComplaint} refresh={refresh} setRefresh={setRefresh} />
            {error && (
                <div className="px-4">
                    <div className="mt-3 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                        <div className="flex justify-between items-center">
                            <span className="">{error}</span>
                            <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                ×
                            </button>
                        </div>
                    </div>
                </div>
            )}
            <div className="flex flex-row justify-end items-start gap-6 p-2 mt-3">
                <SecondaryButton className="xl:px-10">تحويل الى قسم اخر</SecondaryButton>
                <PrimaryButton
                    disabled={apparentComplaint.status === 'resolved'}
                    onClick={handleCloseComplaint}
                    className="xl:px-24"
                >
                    {loading ? 'جاري التحميل...' : 'إغلاق الشكوى'}
                </PrimaryButton>
            </div>
        </div>
    );
}
