import { useState } from 'react';
import { SubtleButton } from '@/components/buttons/SubtleButton';
import { Textarea } from '@/components/ui/textarea';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { Complaint } from '@/types/complaint.types';

interface Props {
    apparentComplaint: Complaint | null;
    refresh: boolean;
    setRefresh: (currentPage: boolean) => void;
}
export default function Reply({ apparentComplaint, refresh, setRefresh }: Props) {
    const [message, setMessage] = useState('');
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);

    const handleReply = async () => {
        setLoading(true);
        setError(null); // Clear previous errors
        try {
            await apiPost({
                endpoint: endpoints.complaint.update + apparentComplaint?.id,
                body: { answer: message, customer_id: apparentComplaint?.customer.id },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });
            setMessage('');
            setError(null);
            setRefresh(!refresh);
        } catch (error: any) {
            // Handle generic error
            let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
            if (error.response) {
                errorMessage = error.response.data.msg || errorMessage;
            } else if (error.request) {
                errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
            }
            setError(errorMessage);
        } finally {
            setLoading(false);
        }
    };
    return (
        <div className="px-4 mt-3">
            <div className="rounded-lg border border-[#93919F] p-2">
                <Textarea
                    placeholder="نص رسالة جديدة...."
                    className="text-[#575155] placeholder:text-[#2D364880] text-base font-medium tracking-normal border-none shadow-none resize-none mb-[10px] custom-scrollbar"
                    onChange={(e) => setMessage(e.target.value)}
                    value={message}
                />
                <SubtleButton
                    disabled={message.trim().length < 1}
                    className="block ms-auto w-32 h-11"
                    onClick={handleReply}
                >
                    {loading ? 'جاري التحميل...' : 'رد'}
                </SubtleButton>
            </div>
            {error && (
                <div className="mt-3 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                    <div className="flex justify-between items-center">
                        <span className="">{error}</span>
                        <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                            ×
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
}
